export default {
	data: function() {
		return {
			pageData: {},
			pageLoad: false,
			price: 0,
			cart_amount: 0,
			id:''
		}
	},
	onLoad: function(ops) {
		this.getPage(ops.id);
		console.log(ops.id)
		this.id = ops.id
	},
	methods: {
		getPage: function(id) {
			wx.cloud.init()
			const db = wx.cloud.database()
			db.collection('goods').where({
				'_id':id
			}).get({
				success: res => {
					console.log(res);
					this.pageData = res.data[0]
					this.pageLoad = true;
					// this.pageLoad = true;表示数据完成可以渲染
				},
				fail: err => {
					wx.showToast({
						icon: 'none',
						title: '查询记录失败'
					})
					console.error('[数据库] [查询记录] 失败：', err)
				}
			})
		},
		dMinus: function() {
			if (this.cart_amount > 1) {
				this.cart_amount--;
			}
		},
		dPlus: function() {
			this.cart_amount++;
		},
		addCart: function() {
			wx.cloud.init()
			const db = wx.cloud.database()
			let form = {
				imgurl:this.pageData.imgurl,
				price:this.pageData.price,
				name:this.pageData.name,
			}
			form.amount = this.cart_amount
			form.total = this.pageData.price*1*this.cart_amount
			form._id = this.id
			console.log(form)
			db.collection('shopcar').add({
				data: form,
				success: function(res) {
					console.log(res)
				}
			})
		},
		goBuy: function() {
			
		}
	}
}
